export default "<script context=\"module\">\n  const example = \"AutoplayGlider\"\n</script>\n\n<script lang=\"ts\">\n  import type {SvelteComponent} from 'svelte'\n  import {onMount, onDestroy} from 'svelte'\n\n  import type {GliderMethods} from '..'\n  import Glider from '..'\n  import Details from './lib/Details.svelte'\n\n  const INTERVAL: number = 5000\n  const MAX:      number = 11\n  let timer:      number = 0\n\n  let gliderRef:  SvelteComponent\n  let gliderJs:   GliderMethods\n\n  const startTimer = () => {\n    timer = setInterval(\n      () => {\n        let index = gliderJs.page\n        if (index < MAX) {\n          index += 1\n        } else {\n          index = 0\n        }\n        gliderJs.scrollItem(index, false)\n      },\n      INTERVAL\n    )\n  }\n\n  const stopTimer = () => {\n    if (timer > 0) {\n      clearInterval(timer)\n      timer = -1\n    }\n  }\n\n  onMount(() => {\n    gliderJs = gliderRef?.getGliderJs()\n\n    if (gliderJs && !timer)\n      startTimer()\n  })\n\n  onDestroy(stopTimer)\n</script>\n\n<div class=\"container\">\n  <Glider\n    class=\"glider-container\"\n    draggable\n    hasDots\n    slidesToShow={1}\n    scrollLock\n    bind:this={gliderRef}\n  >\n    <div class=\"slide\">\n      <span>1</span>\n    </div>\n    <div class=\"slide\">\n      <span>2</span>\n    </div>\n    <div class=\"slide\">\n      <span>3</span>\n    </div>\n    <div class=\"slide\">\n      <span>4</span>\n    </div>\n    <div class=\"slide\">\n      <span>5</span>\n    </div>\n    <div class=\"slide\">\n      <span>6</span>\n    </div>\n    <div class=\"slide\">\n      <span>7</span>\n    </div>\n    <div class=\"slide\">\n      <span>8</span>\n    </div>\n    <div class=\"slide\">\n      <span>9</span>\n    </div>\n    <div class=\"slide\">\n      <span>10</span>\n    </div>\n    <div class=\"slide\">\n      <span>11</span>\n    </div>\n    <div class=\"slide\">\n      <span>12</span>\n    </div>\n  </Glider>\n  <Details {example} />\n</div>\n"